/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  font-family: "Open Sans", Arial, "Helvetica Neue", Helvetica, sans-serif; }

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
.h {
  margin: 0 0 .5em; }

h2, .h2 {
  font-size: 50px; }

/* STYLES */
html,
html * {
  box-sizing: border-box; }

.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 100px 15px; }

.input-group {
  margin: 0 0 30px; }

label {
  display: block;
  margin: 0 0 10px; }

input,
textarea {
  width: 100%;
  height: 40px;
  margin: 0;
  padding: 5px 10px;
  border: 1px solid gray;
  border-radius: 4px; }
  input:focus,
  textarea:focus {
    border-color: black;
    outline: none; }
  input.error,
  textarea.error {
    border-color: red; }

textarea {
  height: auto;
  resize: none; }

select {
  min-width: 200px; }

.error-message {
  color: red;
  font-size: 12px; }

.submit-holder {
  text-align: center;
  margin: 0 0 30px; }

.btn {
  transition: background 0.3s ease-in-out, color 0.3s ease-in-out;
  display: inline-block;
  vertical-align: top;
  background: none;
  box-shadow: none;
  padding: 10px 15px;
  border-radius: 4px;
  min-width: 150px;
  cursor: pointer; }
  .btn:hover {
    background: black;
    color: white; }
  .btn:disabled {
    pointer-events: none; }

.user-table {
  width: 100%;
  margin: 0 0 20px; }
  .user-table caption {
    text-align: left;
    margin: 0 0 5px; }
  .user-table th, .user-table td {
    border: 1px solid black;
    width: 50%;
    height: 30px;
    vertical-align: middle;
    padding: 5px; }
  .user-table th {
    text-align: right; }
